import React from 'react';
import { FaEnvelopeOpen } from 'react-icons/fa';
import { FaInstagram } from 'react-icons/fa';
import { FaGithub } from 'react-icons/fa';
import { FaFacebook } from 'react-icons/fa';
import './SideDrawer.css';

const sideDrawer = props => {

    let drawerClasses = 'side-drawer';
    
    if (props.show) {
        drawerClasses = 'side-drawer open';
    }

    return (
        <nav className={drawerClasses}>
            <ul>
                <li><a href="../public/index.html">Home</a></li>
                <li><a href="../public/index.html">Categories</a></li>
                <div className="socials">
                    <a href="mailto:frostbitecoder@hotmail.com" target="_blank" rel="noopener noreferrer">
                        <FaEnvelopeOpen />
                    </a>
                    <a href="https://instagram.com/aranchai_ing" target="_blank" rel="noopener noreferrer">
                        <FaInstagram />
                    </a>
                    <a href="https://github.com/aranchai" target="_blank" rel="noopener noreferrer">
                        <FaGithub />
                    </a>
                    <a href="https://www.facebook.com/profile.php?id=100005874489988" target="_blank" rel="noopener noreferrer">
                        <FaFacebook />
                    </a>
                </div>
            </ul>
        </nav>
    );
};

export default sideDrawer;