import React from 'react';
import { FaEnvelopeOpen } from 'react-icons/fa';
import { FaInstagram } from 'react-icons/fa';
import { FaGithub } from 'react-icons/fa';
import { FaFacebook } from 'react-icons/fa';
import DrawerToggleButton from '../SideDrawer/DrawerToggleButton';
import './Navbar.css';

const navbar = props => (
    <header className="aing-top">
        <div className="container">
            <nav className="aing-nav">
                <div className="drawerResponsive">
                    <DrawerToggleButton click={props.drawerClickHandler} />
                </div>
                <div className="aing-nav__logo">
                    <a href="../public/index.html">
                        <h1>AING</h1>
                    </a>
                </div>
                <div className="aing-navigation">
                    <ul className="aing-navbar__nav">
                        <li className="aing-nav__item"><a href="../public/index.html" className="aing-nav__link">Home</a></li>
                        <li className="aing-nav__item"><a href="../public/index.html" className="aing-nav__link">Categories</a></li>
                    </ul>
                </div>
                <div className="aing-socials">
                    <a href="mailto:frostbitecoder@hotmail.com" target="_blank" rel="noopener noreferrer">
                        <FaEnvelopeOpen />
                    </a>
                    <a href="https://instagram.com/aranchai_ing" target="_blank" rel="noopener noreferrer">
                        <FaInstagram />
                    </a>
                    <a href="https://github.com/aranchai" target="_blank" rel="noopener noreferrer">
                        <FaGithub />
                    </a>
                    <a href="https://www.facebook.com/profile.php?id=100005874489988" target="_blank" rel="noopener noreferrer">
                        <FaFacebook />
                    </a>
                </div>
            </nav>
        </div>
    </header>
);

export default navbar;